import {Component, Input, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {SelectHostAndClustersPageComponent} from "./select-host-and-clusters-page.component";
import {VsanUiUtils} from "@util/vsan-util";
import {ClaimDisksWitnessHostPage} from "@component/wizard/shared-pages/claim-disks-witness-host-page.component";
import {ReadyToCompletePageComponent} from "./ready-to-complete-page.component";
import {ConfigureSharedWitnessWorkflowBacking} from "@generated/configure-shared-witness-workflow-backing";
import {Logger} from "@util/logger";

@Component({
   selector: "vsan-assign-witness-wizard",
   template: `
      <vsan-flow-wizard
            [title]="'vsan.host.sharedWitness.reassign.wizard.title' | localized"
            [pageSets]="pages"
            [contextProperties]="context"
            [startingTaskProperty]="'configureSharedWitnessHost'"
            [services]="services">
      </vsan-flow-wizard>`,
})
export class ReassignWitnessWizardComponent implements OnInit {

   /**
    * Holds wizard context data, shared between all the pages. Context is provided via:
    * * Input property when the wizard is defined declaratively (e.g., precheck dialog).
    * * Modal context when the wizard is opened via ModalBuilder (e.g., two host clusters view).
    */
   @Input()
   context: any;

   readonly services: any[] = [ConfigureSharedWitnessWorkflowBacking];

   ngOnInit(): void {
      if (!this.context) {
         this.context = VsanUiUtils.getModalContext();
      }
      if (!this.context || !this.context.clustersData)  {
         // This shouldn't happen as dialog is opened only when clustersData is not empty.
         Logger.error("No clusters found in wizard context!");
      }
   }

   readonly pages: PageSet[] = [
      new PageSet([SelectHostAndClustersPageComponent]),
      new PageSet([ClaimDisksWitnessHostPage], (hasDiskGroupsOnWitness) => !hasDiskGroupsOnWitness),
      new PageSet([ReadyToCompletePageComponent]),
   ];
}