import {Component} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {ClaimDisksWitnessHostPage} from "@component/wizard/shared-pages/claim-disks-witness-host-page.component";
import {ReadyToCompletePageComponent} from "./ready-to-complete-page.component";
import {ConfigureSharedWitnessWorkflowBacking} from "@generated/configure-shared-witness-workflow-backing";
import {SelectClustersPageComponent} from "./select-clusters-page.component";
import {ManagedObject} from "@service/managed-object";

@Component({
   selector: "vsan-add-clusters-wizard",
   template: `
      <vsan-flow-wizard
            [title]="'vsan.host.sharedWitness.addClusters.wizard.title' | localized"
            [pageSets]="pages"
            [contextProperties]="context"
            [startingTaskProperty]="'configureSharedWitnessHost'"
            [services]="services">
      </vsan-flow-wizard>`,
})
export class AddClustersWizardComponent {

   /**
    * Holds wizard context data, shared between all the pages.
    */
   // Pass hostRef from wizard as ManagedObject.contextObject is not available when requested from the wizard page
   context: any = {hostRef: ManagedObject.contextObject};
   readonly services: any[] = [ConfigureSharedWitnessWorkflowBacking];

   readonly pages: PageSet[] = [
      new PageSet([SelectClustersPageComponent]),
      new PageSet([ClaimDisksWitnessHostPage]),
      new PageSet([ReadyToCompletePageComponent]),
   ];
}