import {Component} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {InputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {InventoryNode} from "@generated/inventory-node";

@WizardPage(
      VsanUiUtils.getString("vsan.host.sharedWitness.reassign.wizard.summary.title"),
)
@Component({
   templateUrl: "ready-to-complete-page.html",
   styleUrls: ["ready-to-complete-page.scss"],
})
export class ReadyToCompletePageComponent {

   @InputProperty()
   selectedClusters: InventoryNode[];

   @InputProperty()
   witnessHost: ManagedObject;

   @InputProperty()
   witnessHostNode: InventoryNode;

   @InputProperty(InputProperty.required(false))
   witnessHostDiskMapping: VsanDiskMapping;

   ngOnInit() {
      this.selectedClusters = this.selectedClusters.sort((c1, c2) =>
            c1.name.localeCompare(c2.name));
   }

   claimedCache(): number {
      return DiskMgmtUtil.lbaToBytes(this.witnessHostDiskMapping.ssd.capacity);
   }

   claimedCapacity(): number {
      return this.witnessHostDiskMapping.nonSsd.reduce((sum, disk) => sum += DiskMgmtUtil.lbaToBytes(disk.capacity), 0);
   }
}