import {Component, OnInit} from "@angular/core";
import {InventoryNode} from "@component/inventory/inventory-node";
import {AlertType} from "@component/validation/alert-type";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {ConfigureSharedWitnessWorkflowBacking} from "@generated/configure-shared-witness-workflow-backing";
import {DiskManagementService} from "@generated/disk-management-service";
import {FilterContextKey} from "@generated/filter-context-key";
import {InventoryNode as InventoryNodeInfo} from "@generated/inventory-node";
import {SharedWitnessClusterValidationData} from "@generated/shared-witness-cluster-validation-data";
import {VsanClaimDisksData} from "@generated/vsan-claim-disks-data";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {WitnessCandidateInventoryService} from "@generated/witness-candidate-inventory-service";
import {WitnessHostValidationResult} from "@generated/witness-host-validation-result";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {WitnessHostValidation, WitnessValidationService} from "@service/witness-validation.service";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "select-host-and-clusters-page.html",
   styleUrls: ["select-host-and-clusters-page.scss"],
})
@WizardPage(
      VsanUiUtils.getString("vsan.host.sharedWitness.reassign.wizard.selectClusters.title"),
)
export class SelectHostAndClustersPageComponent implements OnInit, OnValidate {

   inventoryRoot: ManagedObject[];
   filterContext: Map<FilterContextKey, ManagedObject>;
   validationMessages: string[] = [];
   validationAlertType: AlertType = AlertType.SUCCESS;
   componentsLimit: number;
   clustersValidationData: Map<string, SharedWitnessClusterValidationData>;
   assignedClustersCount: number = 0;
   witnessAssignedClusters: ManagedObject[] = [];

   // Selected clusters including ones witnessed by the current host
   selectedClustersAll: InventoryNodeInfo[] = [];

   // Flag to track if currently selected host & clusters have been validated against backend
   private skipWitnessValidation: boolean = false;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty(InputProperty.required(false))
   currentWitnessHost: ManagedObject;

   @InputProperty()
   clustersData: InventoryNodeInfo[];

   // Selected clusters excluding ones witnessed by the current host
   @OutputProperty()
   selectedClusters: InventoryNodeInfo[] = [];

   @OutputProperty()
   clustersRefs: ManagedObject[];

   @OutputProperty(OutputProperty.ifNull(VsanUiUtils.getString(
         "vsan.host.sharedWitness.reassign.wizard.selectClusters.validation.selectWitnessHost")))
   witnessHost: ManagedObject;

   @OutputProperty()
   witnessHostNode: InventoryNodeInfo;

   @OutputProperty()
   hasDiskGroupsOnWitness: boolean = false;

   /*
      witnessHostDiskMapping property is set in ClaimDisksWitnessHostPage which, however, is conditional based on
      whether the selected host has disk groups or not.
      Since the property is required for 'startingTaskProperty' method we must always define it.
    */
   @OutputProperty()
   witnessHostDiskMapping: VsanDiskMapping;

   constructor(private witnessHostService: WitnessValidationService,
         public hostInventoryService: WitnessCandidateInventoryService,
         private sharedWitnessService: ConfigureSharedWitnessWorkflowBacking,
         private diskManagementService: DiskManagementService) {
   }

   ngOnInit(): void {
      this.inventoryRoot = [ManagedObject.vcRoot(ManagedObject.contextObject.serverGuid)];
      this.setInventoryFilterContext();
      // Preselect all clusters
      this.selectedClustersAll = this.clustersData;
   }

   private setInventoryFilterContext(): void {
      if (this.currentWitnessHost) {
         const filterContext: Map<FilterContextKey, ManagedObject> = new Map();
         filterContext.set(FilterContextKey.CURRENT_WITNESS_HOST_REF, this.currentWitnessHost);
         this.filterContext = filterContext;
      }
   }

   async hostSelected(event: any): Promise<void> {
      this.closeHostSelectionDropDown();
      const item: InventoryNode = event.node;
      if (this.witnessHost === item.data.nodeRef) {
         // The selected host is already selected, nothing to do.
         return;
      }
      this.assignedClustersCount = 0;
      this.componentsLimit = null;
      this.setHostData(item);
      await this.validateCompatibility(true);
   }

   private setHostData(item: InventoryNode): void {
      if (this.isHost(item)) {
         this.witnessHost = item.data.nodeRef;
         this.witnessHostNode = InventoryNodeInfo.Factory.create(
               item.data.nodeRef, item.data.name, item.data.iconShape);
      } else {
         this.witnessHost = null;
         this.witnessHostNode = null;
      }
   }

   async validate(): Promise<string[]> {
      // Don't start validation if it has already passed and cluster/host selection hasn't been changed
      if (this.skipWitnessValidation) {
         return null;
      }
      await this.validateCompatibility(false);
      if (this.hasValidationError()) {
         // prevent the user from proceeding if the selected host had validation errors
         return this.validationMessages;
      }
      return null;
   }

   async validateCompatibility(setPageControlErrors: boolean): Promise<void> {
      this.resetValidation();
      if (!this.witnessHost) {
         this.validationAlertType = AlertType.DANGER;
         this.validationMessages.push(VsanUiUtils.getString(
               "vsan.host.sharedWitness.reassign.wizard.selectClusters.validation.selectWitnessHost"));
      } else {
         // Query assigned clusters and validate that non assigned clusters are selected
         await this.setAssignedClusters();
         if (!this.selectedClusters.length) {
            this.validationAlertType = AlertType.DANGER;
            this.validationMessages.push(VsanUiUtils.getString(
                  "vsan.host.sharedWitness.reassign.wizard.selectClusters.validation.clusters"));
         }
      }
      if (VsanUiUtils.isEmpty(this.validationMessages)) {
         await this.validateSharedWitnessHost();
      }
      if (!this.hasValidationError()) {
         this.skipWitnessValidation = true;
      } else if (setPageControlErrors) {
         this.pageControls.setValidationErrors(this.validationMessages);
      }
      this.pageControls.rebuildToc();
   }

   private async setAssignedClusters(): Promise<void> {
      this.witnessAssignedClusters = await this.pageControls.collect("witnessAssignedClusters");
      this.assignedClustersCount =
            !VsanUiUtils.isEmpty(this.witnessAssignedClusters) ? this.witnessAssignedClusters.length : 0;
      this.selectedClusters = this.selectedClustersAll.filter(
            selClsInfo => !this.isClusterWitnessedByCurrentHost(selClsInfo));
      this.clustersRefs = this.selectedClusters.map(c => c.moRef);
   }

   isClusterWitnessedByCurrentHost(clsInfo: InventoryNodeInfo): boolean {
      if (!VsanUiUtils.isEmpty(this.witnessAssignedClusters)) {
         return this.witnessAssignedClusters.some(
               assignedClsRef => ManagedObject.areEqual(clsInfo.moRef, assignedClsRef));
      } else {
         return false;
      }
   }

   private async validateSharedWitnessHost(): Promise<void> {
      this.pageControls.setBusyState();
      let witnessHostValidation: WitnessHostValidation;
      try {
         let hostValidationResult: WitnessHostValidationResult;
         let witnessClaimDisksData: VsanClaimDisksData;
         [hostValidationResult, this.componentsLimit, witnessClaimDisksData] = await Promise.all([
            this.sharedWitnessService.getValidateNewWitnessHost(this.clustersRefs, this.witnessHost),
            this.sharedWitnessService.getMaxComponentsPerCluster(this.witnessHost),
            this.diskManagementService.getHostDisksClaimingData(this.witnessHost, false),
         ]);
         this.hasDiskGroupsOnWitness = DiskMgmtUtil.hasDiskGroups(witnessClaimDisksData);
         this.clustersValidationData = this.witnessHostService.updateClustersValidation(
               hostValidationResult.witnessValidationResult, this.clustersValidationData, this.clustersData);
         witnessHostValidation = this.witnessHostService.handleSharedWitnessHostValidationResult(hostValidationResult);
      } catch (error) {
         witnessHostValidation = this.witnessHostService.handleHostValidationError(error);
         this.hasDiskGroupsOnWitness = false;
      }
      this.validationMessages = witnessHostValidation.validationMessages;
      this.validationAlertType = witnessHostValidation.validationAlertType;
      this.pageControls.clearBusyState();
   }

   clusterSelectionChanged(): void {
      // Upon changing the clusters selection we need to call the backend for validation
      this.resetValidation();
   }

   private isHost(item: InventoryNode): boolean {
      return item.data.nodeRef.type === ManagedObjectUtils.HOST ||
            item.data.nodeRef.type === ManagedObjectUtils.COMPUTE_RESOURCE;
   }

   private resetValidation(): void {
      this.validationAlertType = AlertType.SUCCESS;
      this.validationMessages = [];
      this.skipWitnessValidation = false;
      this.clustersRefs = [];
      this.selectedClusters = [];
      this.pageControls.clearValidationErrors();
      this.pageControls.rebuildToc();
   }

   private hasValidationError(): boolean {
      return this.validationMessages && this.validationMessages.length && this.validationAlertType === AlertType.DANGER;
   }

   private closeHostSelectionDropDown(): void {
      // Close dropdown programmatically via element's click.
      // *clrIfOpen="openDropDown" cannot be used because dropdown's contents should always be rendered in order
      // to access WitnessHostSelectionComponent
      document.getElementById("shared-witness-select").click();
   }

   // Get info labels for assigned cluster count and possible witness vLCM disablement
   getInfoLabels(): string[] {
      let assignedClustersMessage: string;
      if (this.assignedClustersCount === 1) {
         assignedClustersMessage = VsanUiUtils.getString(
               "vsan.host.sharedWitness.reassign.wizard.selectClusters.assignedClustersCount.one",
                     this.witnessHostNode.name);
      } else {
         assignedClustersMessage = VsanUiUtils.getString(
               "vsan.host.sharedWitness.reassign.wizard.selectClusters.assignedClustersCount.many",
               this.witnessHostNode.name,
               this.assignedClustersCount);
      }
      if (this.validationAlertType === AlertType.INFO) {
         return [assignedClustersMessage, this.validationMessages[0]];
      } else {
         return [assignedClustersMessage];
      }
   }

   getComponentsLimitLabel(): string | number {
      return VsanUiUtils.isNumericValue(this.componentsLimit) ?
            this.componentsLimit : VsanUiUtils.getString("vsan.na.label");
   }
}
