import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {AlertType} from "@component/validation/alert-type";
import {TwoHostClusterData} from "@generated/two-host-cluster-data";
import {WitnessHostValidationResult} from "@generated/witness-host-validation-result";
import {WitnessHostValidation, WitnessValidationService} from "@service/witness-validation.service";
import {SharedWitnessClusterValidationData} from "@generated/shared-witness-cluster-validation-data";
import {InventoryNode} from "@generated/inventory-node";
import {SharedWitnessLimits} from "@generated/shared-witness-limits";

@Component({
   templateUrl: "select-clusters-page.html",
   styleUrls: ["select-clusters-page.scss"],
})
@WizardPage(
      VsanUiUtils.getString("vsan.host.sharedWitness.addClusters.wizard.selectClusters.title"),
)
export class SelectClustersPageComponent implements OnInit, OnValidate {

   validationMessages: string[] = [];
   validationAlertType: AlertType = AlertType.SUCCESS;
   clustersValidationData: Map<string, SharedWitnessClusterValidationData>;

   // Flag to track if currently selected host & clusters have been validated against backend
   private skipWitnessValidation: boolean = false;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   twoNodeClusters: TwoHostClusterData[] = [];

   @InputProperty()
   sharedWitnessLimits: SharedWitnessLimits;

   // Selected clusters excluding ones witnessed by the current host
   @OutputProperty()
   selectedClusters: InventoryNode[] = [];

   @OutputProperty(OutputProperty.ifEmpty(
         VsanUiUtils.getString("vsan.host.sharedWitness.reassign.wizard.selectClusters.validation.clusters")))
   clustersRefs: ManagedObject[];

   @OutputProperty(OutputProperty.named("witnessHost"))
   @InputProperty()
   hostRef: ManagedObject;

   constructor(private witnessHostService: WitnessValidationService) {
   }

   ngOnInit(): void {
      this.twoNodeClusters = JSON.parse(JSON.stringify(this.twoNodeClusters));
   }

   async validate(): Promise<string[]> {
      // Don't start validation if it has already passed and cluster/host selection hasn't been changed
      if (this.skipWitnessValidation) {
         return null;
      }
      await this.validateCompatibility(false);
      if (this.hasValidationError()) {
         // prevent the user from proceeding if the selected host had validation errors
         return this.validationMessages;
      }
      return null;
   }

   async validateCompatibility(setPageControlErrors: boolean = true): Promise<void> {
      this.resetValidation();
      if (VsanUiUtils.isEmpty(this.validationMessages)) {
         await this.validateSharedWitnessHost();
      }
      if (!this.hasValidationError()) {
         this.skipWitnessValidation = true;
      } else if (setPageControlErrors) {
         this.pageControls.setValidationErrors(this.validationMessages);
      }
      this.pageControls.rebuildToc();
   }

   resetValidation(): void {
      this.validationAlertType = AlertType.SUCCESS;
      this.validationMessages = [];
      this.skipWitnessValidation = false;
      this.pageControls.clearValidationErrors();
      this.pageControls.rebuildToc();
   }

   private async validateSharedWitnessHost(): Promise<void> {
      this.pageControls.setBusyState();
      let witnessHostValidation: WitnessHostValidation;
      try {
         const result: WitnessHostValidationResult = await this.pageControls.collect("validateCurrentWitnessHost");
         this.clustersValidationData = this.witnessHostService.updateClustersValidation(
               result.witnessValidationResult, this.clustersValidationData, this.twoNodeClusters.map(i => i.cluster));
         witnessHostValidation = this.witnessHostService.handleSharedWitnessHostValidationResult(result);
      } catch (error) {
         witnessHostValidation = this.witnessHostService.handleHostValidationError(error);
      }
      this.validationMessages = witnessHostValidation.validationMessages;
      this.validationAlertType = witnessHostValidation.validationAlertType;
      this.pageControls.clearBusyState();
   }

   selectionChanged(clustersRefs: ManagedObject[]): void {
      // Upon changing the clusters selection we need to call the backend for validation
      this.resetValidation();
      this.clustersRefs = clustersRefs;
      this.selectedClusters = clustersRefs.map(this.getClusterDataByRef.bind(this));
      // TODO: pass all clusters
   }

   private getClusterDataByRef(clusterRef: ManagedObject): InventoryNode {
      return this.twoNodeClusters.find(clusterData =>
            ManagedObject.uid(clusterData.cluster.moRef) === ManagedObject.uid(clusterRef)).cluster;
   }

   private hasValidationError(): boolean {
      return !VsanUiUtils.isEmpty(this.validationMessages) && this.validationAlertType === AlertType.DANGER;
   }
}
