import {CommonModule} from "@angular/common";
import {NgModule} from "@angular/core";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {RouterModule, Routes} from "@angular/router";
import {ClarityModule} from "@clr/angular";
import {DiskmgmtCommonModule} from "@component/diskmgmt/diskmgmt-common.module";
import {UnavailableViewModule} from "@component/unavailable-view/unavailable-view.module";
import {FlowWizardModule} from "@component/wizard/flow-wizard.module";
import {WizardSharedPagesModule} from "@component/wizard/shared-pages/wizard-shared-pages.module";
import {VsanCommonModule} from "app/vsan/common/vsan-common.module";
import {NgDragDropModule} from "ng-drag-drop";
import {SharedWitnessCommonModule} from "../common/shared-witness-common.module";
import {SharedWitnessRoutePaths} from "../common/shared-witness-route-paths";
import {AddClustersPrecheckDialogComponent} from "../dialog/add-clusters-precheck-dialog.component";
import {ReassignWitnessPrecheckDialogComponent} from "../dialog/reassign-witness-precheck-dialog.component";
import {AddClustersWizardComponent} from "./add-clusters-wizard.component";
import {ReadyToCompletePageComponent} from "./ready-to-complete-page.component";
import {ReassignWitnessWizardComponent} from "./reassign-witness-wizard.component";
import {SelectClustersPageComponent} from "./select-clusters-page.component";
import {SelectHostAndClustersPageComponent} from "./select-host-and-clusters-page.component";

const sharedWitnessHostWizardRoutes: Routes = [
   {
      path: SharedWitnessRoutePaths.REASSIGN_WITNESS_WIZARD,
      component: ReassignWitnessWizardComponent,
   },
   {
      path: SharedWitnessRoutePaths.ADD_CLUSTERS_WIZARD,
      component: AddClustersWizardComponent,
   },
   {
      path: SharedWitnessRoutePaths.ADD_CLUSTERS_PRECHECK_DIALOG,
      component: AddClustersPrecheckDialogComponent,
   },
   {
      path: SharedWitnessRoutePaths.REASSIGN_WITNESS_PRECHECK_DIALOG,
      component: ReassignWitnessPrecheckDialogComponent,
   },
];

@NgModule({
   imports: [
      CommonModule,
      FormsModule,
      ReactiveFormsModule,
      ClarityModule,
      NgDragDropModule,
      VsanCommonModule,
      FlowWizardModule,
      DiskmgmtCommonModule,
      WizardSharedPagesModule,
      SharedWitnessCommonModule,
      UnavailableViewModule,
      RouterModule.forChild(sharedWitnessHostWizardRoutes),
   ],
   declarations: [
      AddClustersPrecheckDialogComponent,
      AddClustersWizardComponent,
      ReassignWitnessPrecheckDialogComponent,
      ReassignWitnessWizardComponent,
      SelectClustersPageComponent,
      SelectHostAndClustersPageComponent,
      ReadyToCompletePageComponent,
   ],
   entryComponents: [
      AddClustersPrecheckDialogComponent,
      ReassignWitnessPrecheckDialogComponent,
      SelectClustersPageComponent,
      SelectHostAndClustersPageComponent,
      ReadyToCompletePageComponent,
   ],
})
export class SharedWitnessWizardModule {
}