import {Component, OnInit} from "@angular/core";
import {ConfigureSharedWitnessWorkflowBacking} from "@generated/configure-shared-witness-workflow-backing";
import {InventoryNode} from "@generated/inventory-node";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {SharedWitnessConstants} from "../common/shared-witness-constants";

@Component({
   templateUrl: "reassign-witness-precheck-dialog.html",
   selector: "vsan-reassign-witness-precheck-dialog",
   styleUrls: ["reassign-witness-precheck-dialog.scss"],
})
export class ReassignWitnessPrecheckDialogComponent implements OnInit {

   loading: boolean = false;
   showPrecheck: boolean = true;
   validationMessages: string[] = [];
   reassignWizardContext: any;

   constructor(private configureSharedWitnessWorkflowBacking: ConfigureSharedWitnessWorkflowBacking) {
   }

   ngOnInit(): void {
      const clustersRefs: ManagedObject[] = ManagedObject.contextObjects;
      if (clustersRefs.length >= SharedWitnessConstants.MAX_NUMBER_OF_SHARED_CLUSTERS) {
         this.validationMessages = [VsanUiUtils.getString(
               "vsan.cluster.sharedWitness.reassignWitnessPrecheck.dialog.validation.tooManyClusters",
               clustersRefs.length, SharedWitnessConstants.MAX_NUMBER_OF_SHARED_CLUSTERS)];
         return;
      }
      // If there aren't any validation errors hide the current precheck dialog and show reassign witness wizard.
      this.reassignWitness(clustersRefs);
   }

   async reassignWitness(clustersRefs: ManagedObject[]): Promise<void> {
      let clustersData: InventoryNode[];
      try {
         this.loading = true;
         clustersData = await this.configureSharedWitnessWorkflowBacking.getClustersNodes(clustersRefs);
      } catch (e) {
         this.validationMessages = [e];
         return;
      } finally {
         this.loading = false;
      }
      this.reassignWizardContext = {clustersData: clustersData};
      // SDK API allows to modify modal height only and therefore the max width of wizard and dialogs is initially set.
      VsanUiUtils.setModalDialogHeight(700);
      this.showPrecheck = false;
   }
}
