import {Component, OnInit} from "@angular/core";
import {SharedWitnessHostService} from "@generated/shared-witness-host-service";
import {SharedWitnessValidationService} from "@generated/shared-witness-validation-service";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "add-clusters-precheck-dialog.html",
   selector: "vsan-add-clusters-precheck-dialog",
   styleUrls: ["add-clusters-precheck-dialog.scss"],
})
export class AddClustersPrecheckDialogComponent implements OnInit {
   loading: boolean = false;
   showPrecheck: boolean = true;
   hasDiskGroups: boolean = false;
   validationMessage: string;
   witnessHostName: string = "";
   private static DIALOG_HEIGHT_LARGE: number = 700;

   constructor(private sharedWitnessValidationService: SharedWitnessValidationService,
         private sharedWitnessHostService: SharedWitnessHostService,
         private stretchedClusterService: VsanStretchedClusterService,
         private vsanCapabilityProvider: VsanCapabilityProvider) {
   }

   async ngOnInit(): Promise<void> {
      let witnessHostCompatibility: any;
      try {
         this.loading = true;
         const isSharedWitnessSupported: boolean = await this.vsanCapabilityProvider.getIsSharedWitnessSupported(
               ManagedObject.contextObject);
         if (!isSharedWitnessSupported) {
            this.validationMessage = VsanUiUtils.getString("vsan.sharedWitness.notSupported");
            return;
         }
         [witnessHostCompatibility, this.hasDiskGroups] = await Promise.all([
            this.sharedWitnessValidationService.querySharedWitnessPrecheckCompatibility(ManagedObject.contextObject),
            this.stretchedClusterService.hasDiskGroups(ManagedObject.contextObject),
         ]);
      } catch (e) {
         this.validationMessage = e;
         return;
      } finally {
         this.loading = false;
      }
      this.witnessHostName = Object.keys(witnessHostCompatibility)[0];
      const validationError: string = witnessHostCompatibility[this.witnessHostName];
      if (validationError) {
         this.validationMessage = validationError;
         return;
      }

      /*
      If there aren't any validation errors hide the current precheck dialog and show add clusters dialog/wizard.
      SDK API allows to modify modal height only and therefore the max width of wizard and dialogs is initially set.
      */
      VsanUiUtils.setModalDialogHeight(AddClustersPrecheckDialogComponent.DIALOG_HEIGHT_LARGE);
      this.showPrecheck = false;
   }
}